                {{ intro && intro.enabled ? 'ÂêØÁî®' : 'Á¶ÅÁî®' }}
              </view>
            </view>
            <view class="intro-content">
              <text class="intro-text">{{ intro && intro.content ? intro.content : 'Êú™ËÆæÁΩÆÂÜÖÂÆπ' }}</text>
            </view>
            <view class="intro-meta">
              <text class="intro-order">ÊéíÂ∫èÔºö{{ index + 1 }}</text>
            </view>
          </view>
          <view class="intro-actions">
            <button class="action-btn edit" @click="editIntro(index)">ÁºñËæë</button>
            <button class="action-btn delete" @click="deleteIntro(index)">Âà†Èô§</button>
            <button class="action-btn move" @click="moveIntro(index, 'up')" :disabled="index === 0">‚Üë</button>
            <button class="action-btn move" @click="moveIntro(index, 'down')" :disabled="index === intros.length - 1">‚Üì</button>
          </view>
        </view>
      </view>
    </view>

    <!-- Ê∑ªÂä†/ÁºñËæëÂºπÁ™ó -->
    <uni-popup :show="showIntroPopup" type="center" @close="closePopupIntro">
      <view class="popup-content">
        <view class="popup-header">
          <text class="popup-title">{{ isEdit ? 'ÁºñËæë‰ªãÁªç' : 'Ê∑ªÂä†‰ªãÁªç' }}</text>
          <button class="close-btn" @click="closePopup">√ó</button>
        </view>
        

        

        
        <view class="form-group">
          <text class="form-label">ÂÜÖÂÆπ</text>
          <textarea 
            v-model="currentIntro.content"
            placeholder="ËØ∑ËæìÂÖ•‰ªãÁªçÂÜÖÂÆπ" 
            class="form-textarea" 
            maxlength="500"
          />
          <view class="word-count">{{ currentIntro.content.length }}/500</view>
        </view>
        
        <view class="form-group">
          <text class="form-label">ËÉåÊôØÂõæÁâáÔºàÂèØÈÄâÔºâ</text>
          <view class="upload-area" @click="chooseImage">
            <image v-if="currentIntro.bgImage" :src="currentIntro.bgImage" class="upload-preview" mode="aspectFill" />
            <view v-else class="upload-placeholder">
              <text class="upload-icon">üì∑</text>
              <text class="upload-text">ÁÇπÂáª‰∏ä‰º†ËÉåÊôØÂõæÁâá</text>
            </view>
          </view>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÊòæÁ§∫ËÆæÁΩÆ</text>
          <view class="setting-group">
            <view class="setting-item">
              <text class="setting-label">ÊòØÂê¶ÂêØÁî®</text>
              <switch :checked="currentIntro.enabled" @change="onEnabledChange" />
            </view>
            <view class="setting-item">
              <text class="setting-label">ÊòæÁ§∫ËæπÊ°Ü</text>
              <switch :checked="currentIntro.showBorder" @change="onBorderChange" />
            </view>
          </view>
        </view>
        
        <view class="form-group">
          <text class="form-label">Ê†∑ÂºèËÆæÁΩÆ</text>
          <view class="style-group">
            <view class="style-item">
              <text class="style-label">ÊñáÂ≠óÈ¢úËâ≤</text>
              <view class="color-picker">
                <view 
                  v-for="color in colorOptions" 
                  :key="color.value"
                  class="color-item"
                  :class="{ active: currentIntro.textColor === color.value }"
                  :style="{ backgroundColor: color.value }"
                  @click="selectTextColor(color.value)"
                ></view>
              </view>
            </view>
            <view class="style-item">
              <text class="style-label">ËÉåÊôØÈ¢úËâ≤</text>
              <view class="color-picker">
                <view 
                  v-for="bgColor in bgColorOptions" 
                  :key="bgColor.value"
                  class="color-item"
                  :class="{ active: currentIntro.bgColor === bgColor.value }"
                  :style="{ backgroundColor: bgColor.value }"
                  @click="selectBgColor(bgColor.value)"
                ></view>
              </view>
            </view>
          </view>
        </view>
        
        <view class="popup-buttons">
          <button class="popup-btn primary" @click="saveIntro">‰øùÂ≠ò</button>
          <button class="popup-btn" @click="closePopup">ÂèñÊ∂à</button>
        </view>
      </view>
    </uni-popup>
  </view>
</template>

<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue'

export default {
  components: {
    uniPopup
  },
  data() {
    return {
      intros: [],
      currentIntro: {
        content: '',
        bgImage: '',
        enabled: true,
        showBorder: true,
        textColor: '#333333',
        bgColor: '#FFFFFF'
      },
      isEdit: false,
      editIndex: -1,
      loading: false,

      colorOptions: [
        { label: 'ÈªëËâ≤', value: '#333333' },
        { label: 'Ê∑±ÁÅ∞', value: '#666666' },
        { label: 'ËìùËâ≤', value: '#007AFF' },
        { label: 'ÁªøËâ≤', value: '#34C759' },
        { label: 'Ê©ôËâ≤', value: '#FF9500' },
        { label: 'Á∫¢Ëâ≤', value: '#FF3B30' }
      ],
      bgColorOptions: [
        { label: 'ÁôΩËâ≤', value: '#FFFFFF' },
        { label: 'ÊµÖÁÅ∞', value: '#F8F9FA' },
        { label: 'ÊµÖËìù', value: '#E3F2FD' },
        { label: 'ÊµÖÁªø', value: '#E8F5E8' },
        { label: 'ÊµÖÊ©ô', value: '#FFF3E0' },
        { label: 'ÊµÖÁ∫¢', value: '#FFEBEE' }
      ],
      showIntroPopup: false
    }
  },
  onLoad() {
    this.loadIntros()
  },
  methods: {
    // Âä†ËΩΩ‰ªãÁªçÊï∞ÊçÆ
    async loadIntros() {
      try {
        this.loading = true
        const result = await uniCloud.callFunction({
          name: 'getHomeIntros'
        })
        // Á°Æ‰øùÊï∞ÊçÆÊòØÊï∞ÁªÑ‰∏îËøáÊª§ÊéâÊó†ÊïàÈ°π
        const data = result.result.data || []
        this.intros = Array.isArray(data) ? data.filter(item => item && typeof item === 'object') : []
        console.log('Âä†ËΩΩÁöÑ‰ªãÁªçÊï∞ÊçÆ:', this.intros)
      } catch (error) {
        console.error('Âä†ËΩΩ‰ªãÁªçÂ§±Ë¥•:', error)
        this.intros = []
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },



    // ÊòæÁ§∫Ê∑ªÂä†ÂºπÁ™ó
    showAddModal() {
      console.log('ÊòæÁ§∫Ê∑ªÂä†ÂºπÁ™ó')
      this.isEdit = false
      this.editIndex = -1
      this.currentIntro = {
        content: '',
        bgImage: '',
        enabled: true,
        showBorder: true,
        textColor: '#333333',
        bgColor: '#FFFFFF'
      }
      console.log('Ê∑ªÂä†Ê®°Âºè‰∏ãÁöÑcurrentIntro:', this.currentIntro)
      console.log('currentIntro.content:', this.currentIntro.content)
      // ÊòæÁ§∫ÂºπÁ™ó
      this.showIntroPopup = true
      console.log('ÂºπÁ™óÊòæÁ§∫ÂêéÁöÑcurrentIntro:', this.currentIntro)
    },

    // ÁºñËæë‰ªãÁªç
    editIntro(index) {
      console.log('ÁºñËæë‰ªãÁªç:', index)
      const originalIntro = this.intros[index]
      if (!originalIntro) {
        console.error('‰ªãÁªçÊï∞ÊçÆ‰∏çÂ≠òÂú®:', index)
        uni.showToast({
          title: 'Êï∞ÊçÆÈîôËØØ',
          icon: 'none'
        })
        return
      }
      
      console.log('ÂéüÂßã‰ªãÁªçÊï∞ÊçÆ:', originalIntro)
      this.isEdit = true
      this.editIndex = index
      
      // Ê∑±Êã∑Ë¥ùÁ°Æ‰øùÊï∞ÊçÆÊ≠£Á°ÆÂ§çÂà∂
      this.currentIntro = {
        content: originalIntro.content || '',
        bgImage: originalIntro.bgImage || '',
        enabled: originalIntro.enabled !== undefined ? originalIntro.enabled : true,
        showBorder: originalIntro.showBorder !== undefined ? originalIntro.showBorder : true,
        textColor: originalIntro.textColor || '#333333',
        bgColor: originalIntro.bgColor || '#FFFFFF'
      }
      
      console.log('ÁºñËæëÁöÑ‰ªãÁªçÊï∞ÊçÆ:', this.currentIntro)
      console.log('currentIntro.content:', this.currentIntro.content)
      // ÊòæÁ§∫ÂºπÁ™ó
      this.showIntroPopup = true
      console.log('ÂºπÁ™óÊòæÁ§∫ÂêéÁöÑcurrentIntro:', this.currentIntro)
    },

    // Âà†Èô§‰ªãÁªç
    async deleteIntro(index) {
      const intro = this.intros[index]
      if (!intro) {
        console.error('‰ªãÁªçÊï∞ÊçÆ‰∏çÂ≠òÂú®:', index)
        uni.showToast({
          title: 'Êï∞ÊçÆÈîôËØØ',
          icon: 'none'
        })
        return
      }
      
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™‰ªãÁªçÂÜÖÂÆπÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              await uniCloud.callFunction({
                name: 'deleteHomeIntro',
                data: { id: intro._id }
              })
              this.intros.splice(index, 1)
              uni.showToast({
                title: 'Âà†Èô§ÊàêÂäü',
                icon: 'success'
              })
            } catch (error) {
              console.error('Âà†Èô§Â§±Ë¥•:', error)
              uni.showToast({
                title: 'Âà†Èô§Â§±Ë¥•',
                icon: 'none'
              })
            }
          }
        }
      })
    },

    // ÁßªÂä®‰ªãÁªç‰ΩçÁΩÆ
    async moveIntro(index, direction) {
      const newIndex = direction === 'up' ? index - 1 : index + 1
      if (newIndex < 0 || newIndex >= this.intros.length) return
      
      // ‰∫§Êç¢‰ΩçÁΩÆ
      const temp = this.intros[index]
      this.intros[index] = this.intros[newIndex]
      this.intros[newIndex] = temp
      
      // Êõ¥Êñ∞ÊéíÂ∫è
      try {
        await uniCloud.callFunction({
          name: 'updateIntroOrder',
          data: { intros: this.intros }
        })
        uni.showToast({
          title: 'ÊéíÂ∫èÊõ¥Êñ∞ÊàêÂäü',
          icon: 'success'
        })
      } catch (error) {
        console.error('Êõ¥Êñ∞ÊéíÂ∫èÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'Êõ¥Êñ∞Â§±Ë¥•',
          icon: 'none'
        })
      }
    },



    // ÈÄâÊã©ÂõæÁâá
    async chooseImage() {
      try {
        const res = await uni.chooseImage({
          count: 1,
          sizeType: ['compressed'],
          sourceType: ['album', 'camera']
        })
        
        if (res.tempFilePaths && res.tempFilePaths.length > 0) {
          // ‰∏ä‰º†ÂõæÁâáÂà∞‰∫ëÂ≠òÂÇ®
          const uploadResult = await uniCloud.uploadFile({
            filePath: res.tempFilePaths[0],
            cloudPath: `intros/${Date.now()}_${Math.random().toString(36).substr(2, 9)}.jpg`
          })
          
          this.currentIntro.bgImage = uploadResult.fileID
        }
      } catch (error) {
        console.error('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•:', error)
        uni.showToast({
          title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•',
          icon: 'none'
        })
      }
    },

    // ÂêØÁî®Áä∂ÊÄÅÂàáÊç¢
    onEnabledChange(e) {
      this.currentIntro.enabled = e.detail.value
    },

    // ËæπÊ°ÜÊòæÁ§∫ÂàáÊç¢
    onBorderChange(e) {
      this.currentIntro.showBorder = e.detail.value
    },

    // ÈÄâÊã©ÊñáÂ≠óÈ¢úËâ≤
    selectTextColor(color) {
      this.currentIntro.textColor = color
    },

    // ÈÄâÊã©ËÉåÊôØÈ¢úËâ≤
    selectBgColor(bgColor) {
      this.currentIntro.bgColor = bgColor
    },

    // ‰øùÂ≠ò‰ªãÁªç
    async saveIntro() {
      console.log('ÂºÄÂßã‰øùÂ≠ò‰ªãÁªç')
      console.log('ÂΩìÂâç‰ªãÁªçÊï∞ÊçÆ:', this.currentIntro)
      console.log('contentÂÄº:', this.currentIntro.content)
      console.log('contentÊòØÂê¶‰∏∫Á©∫:', !this.currentIntro.content)
      console.log('contentÊòØÂê¶‰∏∫Á©∫Â≠óÁ¨¶‰∏≤:', this.currentIntro.content === '')
      console.log('contentÊòØÂê¶‰∏∫Á©∫Êàñundefined:', !this.currentIntro.content || this.currentIntro.content === '')
      
      if (!this.currentIntro.content || this.currentIntro.content.trim() === '') {
        uni.showToast({
          title: 'ËØ∑Â°´ÂÜôÂÜÖÂÆπ',
          icon: 'none'
        })
        return
      }

      try {
        this.loading = true
        
        if (this.isEdit) {
          console.log('ÊâßË°åÁºñËæëÊìç‰Ωú')
          // Êõ¥Êñ∞‰ªãÁªç
          const updateResult = await uniCloud.callFunction({
            name: 'updateHomeIntro',
            data: {
              id: this.intros[this.editIndex]._id,
              intro: this.currentIntro
            }
          })
          console.log('Êõ¥Êñ∞ÁªìÊûú:', updateResult)
          this.intros[this.editIndex] = { ...this.currentIntro }
        } else {
          console.log('ÊâßË°åÊ∑ªÂä†Êìç‰Ωú')
          console.log('‰º†ÈÄíÁªô‰∫ëÂáΩÊï∞ÁöÑÊï∞ÊçÆ:', { intro: this.currentIntro })
          console.log('currentIntro.content:', this.currentIntro.content)
          console.log('currentIntro.contentÁ±ªÂûã:', typeof this.currentIntro.content)
          console.log('currentIntro.contentÈïøÂ∫¶:', this.currentIntro.content ? this.currentIntro.content.length : 0)
          
          // Ê∑ªÂä†‰ªãÁªç
          const introData = {
            content: this.currentIntro.content,
            bgImage: this.currentIntro.bgImage || '',
            enabled: this.currentIntro.enabled,
            showBorder: this.currentIntro.showBorder,
            textColor: this.currentIntro.textColor,
            bgColor: this.currentIntro.bgColor
          }
          console.log('ÂáÜÂ§áÂèëÈÄÅÁöÑÊï∞ÊçÆ:', introData)
          
          // Â∞ùËØïÁõ¥Êé•‰º†ÈÄíÊï∞ÊçÆ
          const result = await uniCloud.callFunction({
            name: 'addHomeIntro',
            data: introData
          })
          console.log('Ê∑ªÂä†ÁªìÊûú:', result)
          
          if (result.result && result.result.code === 0) {
            this.intros.push(result.result.data)
            console.log('Ê∑ªÂä†ÊàêÂäüÔºåÂΩìÂâçÂàóË°®:', this.intros)
          } else {
            throw new Error(result.result?.message || 'Ê∑ªÂä†Â§±Ë¥•')
          }
        }

        this.closePopup()
        uni.showToast({
          title: this.isEdit ? 'Êõ¥Êñ∞ÊàêÂäü' : 'Ê∑ªÂä†ÊàêÂäü',
          icon: 'success'
        })
      } catch (error) {
        console.error('‰øùÂ≠òÂ§±Ë¥•:', error)
        uni.showToast({
          title: '‰øùÂ≠òÂ§±Ë¥•: ' + (error.message || error),
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },



    // ÂÖ≥Èó≠ÂºπÁ™ó
    closePopup() {
      this.showIntroPopup = false
    },
    closePopupIntro() {
      this.showIntroPopup = false
    }
  }
}
</script>

